import {_decorator, Component, game, Mesh, MeshRenderer, Node, profiler, Quat} from 'cc';

const {ccclass, property} = _decorator;

class Size {
  values: number[];
  public constructor(width: number, height: number) {
    this.values = [width, height];
  }
  get x(): number {
    return this.values[0];
  }
  get y(): number {
    return this.values[1];
  }
}

class Game_Board {
  grid_size: Size;
  renderable: MeshRenderer;

  public constructor(grid_size: Size, renderable: MeshRenderer) {
    this.grid_size = grid_size;
    this.renderable = renderable;
  }
  public render() {
    this.renderable.material.setProperty(
        'tilling_offset', new Quat(8, 8, 0, 0));
  }
}

@ccclass('Main')
export class Main extends Component {
  @property(MeshRenderer) board_renderable;
  start() {
    let game_board = new Game_Board(new Size(8, 8), this.board_renderable);

    game_board.render();
  }
}
